<template>
  <div>
    <div>
      <div class="main animated zoomIn">
        <img src="../../../static/images/z2.jpg" alt="" class="favicon animated rotateIn" />
        <h1>ZswBlog</h1>
        <h2></h2>
      </div>
      <hr />
      <div class="location">
        <p id="main-site">
          <i class="fa fa-hourglass-end" aria-hidden="true"></i>
          <a href="http://www.zswblog.xyz">
            <span>www.zswblog.xyz</span>
          </a>
        </p>
      </div>
      <div class="date">
        <p id="main-date">
          <i class="fa fa-clock-o" aria-hidden="true"></i>
          <span id="showtime"></span>
        </p>
      </div>
      <div class="about">
        <p class="about-site">
          2.0GithubGiteestar
          <br />
          <span>
            .Net MVC+EF+SqlServer2008,
            
            .Net Core WebApi+Mysql,
            ,
            IIS,Linux,
            Centos 7+Nginx
            Autofac,AutoMapper,EFCore,,DDD,Redis
            
            SpringBootSEOHTML
            SEO
          </span>
          <br />
          <span>
            1.0like shit,
            jquery+vue+layui,js
            Vue
            webpack+node,
            
            Node,Webpack,VueCli3.0,Element-Ui,
          </span>
        </p>
        <p class="about-thanks animated swing">
          :
          <br />
          <span>---"",</span>
          <br />
          <span>
            ,
            Vue,
            LayuiElement-UI,
          </span>
        </p>
        <p>
          <el-calendar v-model="value"></el-calendar>
        </p>
      </div>
    </div>
  </div>
</template>

<script>
import { setInterval, clearInterval } from "timers";

export default {
  name: "web-site",
  data() {
    return {
      timer: "",
      value: new Date()
    };
  },
  mounted() {
    clearInterval(this.timer);
    this.timer = setInterval(this.showDateTime, 1000);
  },
  methods: {
    showDateTime() {
      let BirthDay = new Date("10/08/2019 00:00:00");
      let today = new Date();
      let timeold = today.getTime() - BirthDay.getTime();
      let sectimeold = timeold / 1000;
      let secondsold = Math.floor(sectimeold);
      let msPerDay = 24 * 60 * 60 * 1000;
      let e_daysold = timeold / msPerDay;
      let daysold = Math.floor(e_daysold);
      let e_hrsold = (e_daysold - daysold) * 24;
      let hrsold = Math.floor(e_hrsold);
      let e_minsold = (e_hrsold - hrsold) * 60;
      let minsold = Math.floor((e_hrsold - hrsold) * 60);
      let seconds = Math.floor((e_minsold - minsold) * 60);
      showtime.innerHTML =
        "" +
        daysold +
        "" +
        hrsold +
        "" +
        minsold +
        "" +
        seconds +
        "";
    }
  },
  beforeDestroy() {
    clearInterval(this.timer);
  }
};
</script>

<style scoped>
h1,
h2,
h4,
p,
a {
  font-weight: normal;
  font-family: "Microsoft YaHei";
}
.favicon {
  height: 130px;
  width: 130px;
  border-radius: 50%;
}
.main,
.location,
.date {
  text-align: center;
  display: block;
}
.location,
.date {
  width: 500px;
  margin: 0 auto;
  line-height: 30px;
  margin-top: 15px;
}
.location span,
.date span {
  font-size: 22px;
}
h4 {
  margin-left: 2em;
  float: left;
  width: 100%;
}
i {
  line-height: 30px;
}
.about-site {
  margin-left: 4em;
  font-size: 24px;
}
.about-site > span {
  text-indent: 2em;
  float: left;
  margin-bottom: 15px;
  font-size: 18px;
}
.about-thanks {
  margin-left: 4em;
  font-size: 24px;
}
.about-thanks > span {
  text-indent: 2em;
  float: left;
  margin-bottom: 25px;
  font-size: 18px;
}
a {
  text-decoration: none;
  color: #f25b67;
}
@media screen and (max-width: 475px) {
  .about-site,
  .about-thanks {
    margin-left: 1em !important;
  }
  .location,
  .date {
    width: 97% !important;
  }
}
</style>


