<template>
  <div>
    <el-form
      :inline="true"
      ref="tagApplyForm"
      :model="formInline"
      :rules="rules"
      class="demo-form-inline"
    >
      <el-form-item label="" prop="tagTitle">
        <el-input v-model="formInline.tagTitle" placeholder=""></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="beforeSubmit"></el-button>
      </el-form-item>
    </el-form>
    <tag-cloud :data="hotTag" @clickTag="clickTagItem" :config="option"></tag-cloud>
  </div>
</template>

<script>
import { getAllSiteTag, addSiteTag } from "../../../static/js/api/about.api";
export default {
  data() {
    return {
      rules: {
        tagTitle: [{ required: true, message: "", trigger: "blur" }]
      },
      option: {
        radius: 300, // px
        maxFont: 30, // 
        color: null, // 
        rotateAngleXbase: 1000, // 
        rotateAngleYbase: 1000,
        hover: true // 
      },
      formInline: {
        tagTitle: ""
      },
      hotTag: [],
      userId: 0
    };
  },
  mounted() {
    const h = this.$createElement;
    this.$notify({
      title: "",
      message: h(
        "i",
        { style: "color: #296fc7" },
        "\r\n"
      )
    });
    this.getSiteTags();
  },
  methods: {
    async getSiteTags() {
      await getAllSiteTag().then(e => {
        this.hotTag = e;
      });
    },
    clickTagItem(tag) {
      let str = "" + tag.name + "";
      this.$message({ message: str, type: "success" });
    },
    onSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let sitetag = {
            SitetagTitle: this.formInline.tagTitle,
            UserId: this.userId
          };
          addSiteTag(sitetag).then(e => {
            if (e.code == 200) {
              this.$message({
                message: ",",
                type: "success"
              });
            } else {
              this.$message({
                message: ",",
                type: "error"
              });
            }
          });
        } else {
          this.$message({
            message: ",",
            type: "error"
          });
        }
      });
    },
    beforeSubmit() {
      let userId = document.getElementById("userId").innerText;
      if (userId !== "0" && userId !== 0) {
        this.userId = userId;
        this.onSubmit("tagApplyForm");
      } else {
        this.$message({
          message: "",
          type: "error"
        });
      }
    }
  }
};
</script>

<style scoped>
.el-form {
  text-align: center;
  margin: 20px 0;
}
.tag-cloud {
  width: 700px !important;
  height: 700px !important;
}
.tag-cloud > p:hover {
  cursor: pointer;
  border: #45aaa4 solid 1px;
  color: black;
  transition: 0.5s;
}
</style>


